.Wine-div {
    max-width: 450px;
    background-color: #c77781;
    padding: 10px;
    margin: 0 auto 20px auto;
    -webkit-box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
            box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Wine-header {
    width: 100%;
    color: whitesmoke;
}

.Wine-name,
.Wine-year,
.Wine-varietal,
.Wine-gift,
.Wine-price,
.Wine-score {
    padding: 5px;
    -ms-flex-pack: column;
        justify-content: column;
}

.Wine-text {
    padding-top: 5px;
    color: #e4bcc1;
}

#delete-wine-button {
    background: #e4bcc1;
    color: #843640;
    font-size: 16px;
    margin: 20px 10px 15px;
    /* width: 15%; */
}

#wineid {
  font-size: 10px;
}



